import { Fragment, MouseEvent, useState } from "react";
import {
  Menu,
  MenuItem,
  DialogTitle,
  DialogContent,
  DialogActions,
  Dialog,
  Button,
  ListItem,
  ListItemText,
  ListItemIcon,
  IconButton,
} from "@mui/material";
import { MoreVert } from "@mui/icons-material";

import BusinessApi from "Apis/BusinessApi";
import { useSnacks } from "Contexts/Snack";
import { useSettingsApi } from "Contexts/Business";
import { LeadDataFieldInterface } from "Shared/Interfaces/Business/BusinessSettings";

const LDFItem = ({
  display_name,
  field_name,
  field_type,
  id,
}: LeadDataFieldInterface) => {
  const { setErrorMessage, setSuccessMessage } = useSnacks();
  const { setEditLdfId, setShowLdfForm } = useSettingsApi();
  // 3dot menu state
  const [anchorEl, setAnchorEl] = useState<HTMLButtonElement | null>(null);

  //dialog state for delete
  const [openConfirmDialog, setOpenConfirmDialog] = useState(false);

  //function for opening confirm box for delete
  const handleClickOpen = () => {
    setOpenConfirmDialog(true);
  };
  //function for closing confirm box for delete
  const handleClickClose = () => {
    setOpenConfirmDialog(false);
  };
  //function for opening 3 dot menu
  const handleClick = (event: MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  //function for closing 3 dot menu
  const handleClose = () => {
    setAnchorEl(null);
  };

  const deleteLDF = async () => {
    try {
      const res = await BusinessApi.deleteLeadDataField(id);
      if (!res.success) {
        throw Error(res.message);
      }
      setSuccessMessage(res.message);
      setTimeout(() => window.location.reload(), 1000);
    } catch (error: any) {
      setErrorMessage(error.message);
    } finally {
      handleClickClose();
    }
  };

  return (
    <Fragment>
      <ListItem component="div" sx={{ px: { xs: 0, md: 2 } }}>
        <ListItemText
          primary={
            <div className="primary-block">
              <h2 className={"list-title"}>{display_name}</h2>
              <h2 className={"type"}>
                {field_type === "string" ? "Text" : field_type}
              </h2>
            </div>
          }
          secondary={`[${field_name}]`}
          sx={{
            "& .primary-block": {
              alignItems: "center",
              display: "flex",
              flexWrap: "wrap",
              gap: 1.25,
              marginBottom: 0.75,

              "& .list-title": {
                color: "text.primary",
                fontSize: 15,
                fontWeight: 500,
                lineHeight: "18px",
              },

              "& .type": {
                color: "#767676b3",
                fontSize: 12,
                fontWeight: 400,
                lineHeight: 1,
                textTransform: "uppercase",
              },
            },

            "& .MuiListItemText-secondary": {
              color: "text.primary",
              fontSize: 12,
              maxWidth: 290,
              fontWeight: 400,
              lineHeight: "15px",
            },
          }}
        />
        <ListItemIcon>
          <IconButton
            // disabled
            onClick={handleClick}
          >
            <MoreVert />
          </IconButton>
        </ListItemIcon>
      </ListItem>
      {Boolean(anchorEl) && (
        <Menu
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          onClose={handleClose}
          keepMounted
          MenuListProps={{
            "aria-labelledby": "Options",
          }}
          anchorOrigin={{
            vertical: "top",
            horizontal: "right",
          }}
          transformOrigin={{
            vertical: "top",
            horizontal: "right",
          }}
          sx={{
            "& .MuiPaper-root": { minWidth: 120 },
          }}
        >
          <MenuItem
            onClick={() => {
              setEditLdfId(id);
              setShowLdfForm(true);
              handleClose();
            }}
          >
            {"Edit"}
          </MenuItem>
          <MenuItem
            onClick={() => {
              handleClickOpen();
              handleClose();
            }}
          >
            {"Delete"}
          </MenuItem>
        </Menu>
      )}
      <Dialog
        open={openConfirmDialog}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">{"Delete"}</DialogTitle>
        <DialogContent>{"Are you sure you want to delete?"}</DialogContent>
        <DialogActions>
          <Button
            color="secondary"
            onClick={() => {
              handleClickClose();
            }}
          >
            {"Cancel"}
          </Button>
          <Button
            onClick={async () => {
              await deleteLDF();
            }}
            autoFocus
          >
            {"Delete"}
          </Button>
        </DialogActions>
      </Dialog>
    </Fragment>
  );
};

export default LDFItem;
