import { ChangeEvent, Fragment, useCallback, useEffect, useState } from "react";
import { Helmet } from "react-helmet-async";
import { Link } from "react-router-dom";
import { Card, CardContent, Divider, IconButton } from "@mui/material";
import { Clear } from "@mui/icons-material";

import BusinessApi from "Apis/BusinessApi";
import { useSettingsApi, useSettingsData } from "Contexts/Business";
import { useSnacks } from "Contexts/Snack";
import {
  ActionButton,
  CardSX,
  CardContentSX,
  Content,
  ContentInner,
  InnerWrapper,
  ContentHeader,
  HeaderTitle,
  InnerBlock,
  Cta,
  CtaInfo,
  CtaInfoText,
  SearchWrapper,
  SearchBlock,
  SearchInput,
  InnerWrapperSX,
  ContentFooter,
  ContentInnerSX,
  ContentHeaderSX,
} from "Containers/BusinessSettings/LayoutComponents";
import { LeftArrow, Search } from "Assets/Icons";
import LDFItem from "./LeadDataFields/LDFItem";
import LDFHeader from "./LeadDataFields/LDFHeader";
import LeadDataForm from "./LeadDataFields/LeadDataForm";
import { useGlobal } from "Contexts/App";

const LeadDataFields = () => {
  const { isMobile, isTablet } = useGlobal();
  const { setErrorMessage } = useSnacks();
  const [loading, setLoading] = useState<boolean>(true);
  const [isSearchActive, setSearchActive] = useState<boolean>(false);
  const { leadDataFields, ldfFilter, showLdfForm } = useSettingsData();
  const { setLeadDataFields, setLdfFilter, setShowLdfForm, setEditLdfId } =
    useSettingsApi();
  const fetchLeadDataFields = useCallback(async () => {
    try {
      const res = await BusinessApi.getLeadDataFields();
      if (!res.success) {
        throw Error(res.message);
      }
      setLeadDataFields(res.payload);
      setLoading(false);
    } catch (error: any) {
      setErrorMessage(error.message);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    fetchLeadDataFields();
  }, [fetchLeadDataFields]);

  return (
    <Fragment>
      <Helmet>
        <title>Lead Data Fields</title>
      </Helmet>
      <Content>
        {isMobile && (
          <ContentHeader sx={ContentHeaderSX}>
            {!isSearchActive && (
              <IconButton
                component={Link}
                to="/business-settings"
                sx={{ width: 32, height: 32 }}
              >
                <LeftArrow />
              </IconButton>
            )}
            {!isSearchActive && <HeaderTitle>Lead Data Fields</HeaderTitle>}
            {isSearchActive && (
              <SearchWrapper>
                <SearchBlock>
                  <IconButton
                    disableTouchRipple
                    disableFocusRipple
                    disableRipple
                    sx={{ position: "absolute" }}
                  >
                    <Search />
                  </IconButton>
                  <SearchInput
                    onChange={({
                      target: { value },
                    }: ChangeEvent<HTMLInputElement>) => setLdfFilter(value)}
                    placeholder="Search Field"
                  />
                  <IconButton
                    onClick={() => {
                      setLdfFilter("");
                      setSearchActive(!isSearchActive);
                    }}
                    sx={{ position: "absolute", right: 0, top: 0 }}
                  >
                    <Clear />
                  </IconButton>
                </SearchBlock>
              </SearchWrapper>
            )}
            {!isSearchActive && leadDataFields.length > 0 && (
              <IconButton
                onClick={() => setSearchActive((prevState) => !prevState)}
              >
                <Search />
              </IconButton>
            )}
          </ContentHeader>
        )}
        <ContentInner sx={ContentInnerSX}>
          <InnerWrapper sx={InnerWrapperSX}>
            {!loading && (
              <Card elevation={0} sx={CardSX}>
                <CardContent sx={CardContentSX}>
                  {leadDataFields.length === 0 ? (
                    <InnerBlock>
                      <Cta>
                        <ActionButton
                          disableIcon
                          onClick={() => setShowLdfForm(true)}
                          sx={{
                            borderRadius: 2,
                            boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
                            fontSize: 16,
                            fontWeight: 700,
                            height: 36,
                            lineHeight: "19px",
                            margin: "auto",
                            textTransform: "capitalize",
                          }}
                        >
                          {"New Lead Data Field"}
                        </ActionButton>
                      </Cta>
                      <CtaInfo>
                        <CtaInfoText>
                          {
                            "Lead Data Field creates an input field for your user, where the value input by a user can be stored and be later retrieved for different users to draw an analytical comparison or as a future reference."
                          }
                        </CtaInfoText>
                      </CtaInfo>
                    </InnerBlock>
                  ) : (
                    <Fragment>
                      {isTablet && (
                        <LDFHeader {...{ isSearchActive, setSearchActive }} />
                      )}
                      {isTablet && <Divider />}
                      {leadDataFields
                        .filter((ldf: any) =>
                          ldf?.display_name
                            ?.toLowerCase()
                            .includes(ldfFilter?.toLowerCase())
                        )
                        .reverse()
                        .map((lDF, i: number) => (
                          <LDFItem key={i} {...lDF} />
                        ))}
                    </Fragment>
                  )}
                </CardContent>
              </Card>
            )}
          </InnerWrapper>
        </ContentInner>
        {isMobile && leadDataFields.length > 0 && (
          <ContentFooter>
            <ActionButton
              onClick={() => setShowLdfForm(true)}
              variant="outlined"
              color="primary"
              disableIcon
            >
              New Lead Data Field
            </ActionButton>
          </ContentFooter>
        )}
      </Content>
      <LeadDataForm
        open={showLdfForm}
        closeDrawer={() => {
          setEditLdfId(undefined);
          setShowLdfForm(false);
        }}
      />
    </Fragment>
  );
};

export default LeadDataFields;
