import { Autocomplete, TextField, TextFieldProps } from "@mui/material";
import { FC } from "react";
import {
  Control,
  Controller,
  UseControllerProps,
  UseFormSetValue,
} from "react-hook-form";

interface FormTagFieldProps extends UseControllerProps {
  control: Control<any, any>;
  setValue: UseFormSetValue<any>;
  defaultValue: readonly any[];
}

const FormTagField: FC<TextFieldProps & FormTagFieldProps> = ({
  control,
  defaultValue,
  rules,
  name,
  children,
  setValue,
  ...props
}) => {
  return (
    <Controller
      rules={rules}
      control={control}
      name={name}
      render={({ field }) => (
        <Autocomplete
          multiple={true}
          options={defaultValue}
          onChange={(_, newValue: any[]) => {
            setValue(field.name, newValue);
          }}
          freeSolo
          renderInput={(params) => (
            <TextField
              {...params}
              variant="filled"
              label={props.label}
              sx={props.sx}
            />
          )}
        />
      )}
    />
  );
};

export default FormTagField;
