import { ChangeEvent, Dispatch, SetStateAction } from "react";
import { IconButton } from "@mui/material";
import { Clear } from "@mui/icons-material";

import {
  ActionButton,
  SearchBlock,
  SearchInput,
  SearchWrapper,
  TeamHead,
  TeamHeadBottom,
  TeamHeadBottomLeft,
  TeamHeadBottomRight,
  TeamHeadTitle,
  TeamHeadTop,
} from "Containers/BusinessSettings/LayoutComponents";
import { Search } from "Assets/Icons";
import { useSettingsApi } from "Contexts/Business";

const LDFHeader = ({
  isSearchActive,
  setSearchActive,
}: {
  isSearchActive: boolean;
  setSearchActive: Dispatch<SetStateAction<boolean>>;
}) => {
  const { setLdfFilter, setShowLdfForm } = useSettingsApi();

  return (
    <TeamHead>
      <TeamHeadTop>
        <ActionButton
          disableIcon
          type="button"
          onClick={() => setShowLdfForm(true)}
          sx={{ py: 0, px: 0.625, height: 36, mt: 0 }}
        >
          {"New Lead Data Field"}
        </ActionButton>
      </TeamHeadTop>
      <TeamHeadBottom>
        {!isSearchActive && (
          <TeamHeadBottomLeft>
            <TeamHeadTitle>Lead Data Fields</TeamHeadTitle>
          </TeamHeadBottomLeft>
        )}
        <TeamHeadBottomRight>
          {isSearchActive && (
            <SearchWrapper>
              <SearchBlock>
                <IconButton
                  disableTouchRipple
                  disableFocusRipple
                  disableRipple
                  sx={{ position: "absolute" }}
                >
                  <Search />
                </IconButton>
                <SearchInput
                  onChange={({
                    target: { value },
                  }: ChangeEvent<HTMLInputElement>) => setLdfFilter(value)}
                  placeholder="Search Field"
                />
                <IconButton
                  onClick={() => {
                    setLdfFilter("");
                    setSearchActive(!isSearchActive);
                  }}
                  sx={{ position: "absolute", right: 0, top: 0 }}
                >
                  <Clear />
                </IconButton>
              </SearchBlock>
            </SearchWrapper>
          )}
          {!isSearchActive && (
            <IconButton
              onClick={() => setSearchActive((prevState) => !prevState)}
            >
              <Search />
            </IconButton>
          )}
        </TeamHeadBottomRight>
      </TeamHeadBottom>
    </TeamHead>
  );
};

export default LDFHeader;
