import { useCallback, useEffect } from "react";
import { Drawer, IconButton, MenuItem } from "@mui/material";
import { useForm } from "react-hook-form";

import {
  ActionArea,
  ActionButton,
  ContentHeader,
  DrawerForm,
  DrawerWrapper,
  Field,
  FlexRow,
  FormSelectFieldSX,
  FormTagFieldSX,
  FormTextFieldSX,
  GrowColumn,
  HeaderTitle,
} from "Containers/BusinessSettings/LayoutComponents";
import { useSettingsData } from "Contexts/Business";
import { useSnacks } from "Contexts/Snack";
import { isBackDropOrEscapeKey } from "Utils";
import { LeftArrow } from "Assets/Icons";
import FormIOSSwitch from "SharedComponents/FormComponents/FormIOSSwitch";
import FormTextField from "SharedComponents/FormComponents/FormTextField";
import FormTagField from "SharedComponents/FormComponents/FormTagField";
import BusinessApi from "Apis/BusinessApi";

interface LeadDataFormProps {
  open: boolean;
  closeDrawer: () => void;
}

const LeadDataForm = ({ open, closeDrawer }: LeadDataFormProps) => {
  const { setErrorMessage, setSuccessMessage } = useSnacks();
  const { editLdfId } = useSettingsData();
  const { control, handleSubmit, watch, reset, setValue } = useForm<any>({
    defaultValues: {
      field_type: "",
      display_name: "",
      field_name: "",
      display_in_filter: false,
      use_lead_title: false,
    },
  });
  const { field_type, display_name, field_options } = watch();
  const onSubmit = async (data: any) => {
    try {
      console.log(data);
      if (editLdfId) {
        const res = await BusinessApi.updateLeadDataField(editLdfId, data);
        if (!res.success) {
          throw Error(res.message);
        }
        setSuccessMessage(res.message);
        setTimeout(() => window.location.reload(), 1000);
      } else {
        const res = await BusinessApi.createLeadDataField(data);
        if (!res.success) {
          throw Error(res.message);
        }
        setSuccessMessage(res.message);
        setTimeout(() => window.location.reload(), 1000);
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    } finally {
      closeDrawer();
    }
  };
  const displayNameChanged = useCallback(
    () =>
      setValue("field_name", display_name.toLowerCase().replaceAll(" ", "_")),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [display_name]
  );
  const fetchLeadDataField = useCallback(
    async () => {
      try {
        if (editLdfId) {
          const res = await BusinessApi.getLeadDataField(editLdfId);
          if (!res.success) {
            throw Error(res.message);
          }
          reset({
            field_type: res.payload.field_type,
            display_name: res.payload.display_name,
            field_name: res.payload.field_name,
            display_in_filter: res.payload.display_in_filter,
            use_lead_title: res.payload.mark_as_lead_title,
            field_options: res.payload.field_options
              ? res.payload.field_options
              : [],
          });
        }
      } catch (error: any) {
        console.error(error.message);
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [editLdfId]
  );

  useEffect(() => {
    displayNameChanged();
  }, [displayNameChanged]);

  useEffect(() => {
    fetchLeadDataField();
  }, [fetchLeadDataField]);

  return (
    <Drawer
      anchor={"right"}
      open={open}
      onClose={(_, reason) => !isBackDropOrEscapeKey(reason) && closeDrawer()}
      sx={{ zIndex: "modal" }}
    >
      <DrawerWrapper sx={{ width: { xs: "100vw", md: 360 } }}>
        <ContentHeader>
          <IconButton onClick={closeDrawer} sx={{ width: 32, height: 32 }}>
            <LeftArrow />
          </IconButton>
          <HeaderTitle>
            {`${editLdfId ? "Edit" : "Create a"} Data Field`}
          </HeaderTitle>
        </ContentHeader>
        <DrawerForm onSubmit={handleSubmit(onSubmit)}>
          <FlexRow>
            <GrowColumn>
              <Field>
                <FormTextField
                  select
                  control={control}
                  label="Data Field Type"
                  name="field_type"
                  sx={FormSelectFieldSX}
                  required={true}
                  defaultValue={field_type}
                >
                  <MenuItem value="" disabled>
                    {"Select Data Field Type"}
                  </MenuItem>
                  <MenuItem value="numeric">{"Number"}</MenuItem>
                  <MenuItem value="string">{"Text"}</MenuItem>
                  <MenuItem value="boolean">{"Boolean"}</MenuItem>
                  <MenuItem value="date">{"Date"}</MenuItem>
                </FormTextField>
              </Field>
              <Field>
                <FormTextField
                  control={control}
                  name="display_name"
                  label="Display Name"
                  placeholder="Display Name"
                  required={true}
                  sx={FormTextFieldSX}
                />
              </Field>
              <Field>
                <FormTextField
                  control={control}
                  name="field_name"
                  label="Field Name"
                  placeholder="Field Name"
                  required={true}
                  sx={FormTextFieldSX}
                />
              </Field>
              <Field>
                <FormTagField
                  control={control}
                  setValue={setValue}
                  defaultValue={field_options ?? []}
                  name="field_options"
                  label="Options"
                  sx={FormTagFieldSX}
                />
              </Field>
              <Field>
                <div className="d-flex justify-content-between">
                  <div className="grow">
                    <h5 className="field-title">
                      {"Display Field in Filters & Analytics?"}
                    </h5>
                    <small className="field-subtitle">
                      {
                        "This field will be displayed in filter option and analytics can be drawn on this field."
                      }
                    </small>
                  </div>
                  <FormIOSSwitch control={control} name={"display_in_filter"} />
                </div>
              </Field>
              <Field>
                <div className="d-flex justify-content-between">
                  <div className="grow">
                    <h5 className="field-title">{"Use as Lead Title"}</h5>
                    <small className="field-subtitle">
                      {
                        "If a value is available in the field it will show as the default title instead of the phone number in the Chat section."
                      }
                    </small>
                  </div>
                  <FormIOSSwitch control={control} name={"use_lead_title"} />
                </div>
              </Field>
            </GrowColumn>
          </FlexRow>
          <ActionArea>
            <ActionButton type="submit">Save</ActionButton>
          </ActionArea>
        </DrawerForm>
      </DrawerWrapper>
    </Drawer>
  );
};

export default LeadDataForm;
